import React, { useState, useEffect } from 'react';
import { Form, FormGroup, Label, Input, Col, Row, Button } from 'reactstrap';
import { Formik, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import DashboardCard from 'src/components/DashboardCard';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import store from '../../../../store';
const PostNewJob = ({ history }) => {
    const [categories, setCategories] = useState([]);
    const [subCategories, setSubCategories] = useState([]);
    const [categoryName, handleCategoryName] = useState(" ");
    const [file, setFile] = useState('');
    const [filename, setFilename] = useState('');
    const { form, handleSubmit, errors } = useForm();
    const user = store.getState();
    const [service, setService] = useState({
        JobTitle: '',
        description: '',
        rating: [],
        Salary: '',
        serviceImage: null,
        subCategoryID: '',
        categoryName: '',
        avgRating: 'new post',
    });

    var config2 = {
        headers: {
            'Authorization': user.auth.token
        }
    }

    useEffect(() => {
        var tempoC = '';
        axios.get('https://www.togedoortestgo.site/graphql/categories').then(async (response) => {
            tempoC = response.data;
            console.log(response.data);
            setCategories(tempoC);
            console.log(tempoC[0].name.split(' ').join('-'));
            console.log(tempoC[0].subcategories[0]._id);
            const res = await axios.get(`https://www.togedoortestgo.site/graphql/categories/name/Agriculture & Natural Resources`);
            setSubCategories(res.data.subcategories);
            setService({
                ...service,
                categoryName: tempoC[0].name.split(' ').join('-'),
                subCategoryID: tempoC[0].subcategories[0]._id,
            });
            console.log(' useEffect 2');
        });
    }, []);

    useEffect(() => {
        async function fetchData() {
            const response = await axios.get(
                `https://www.togedoortestgo.site/graphql/categories/name/${categoryName.split(' ').join('-')}`
            );

            setSubCategories(response.data.subcategories);
            setService({ ...service, subCategoryID: response.data.subcategories[0]._id });
        }

        fetchData();
    }, [categoryName]);

    const onChange = (e) => {
        e.persist();
        setService({ ...service, [e.target.name]: e.target.value }); //////////////////////////////
        console.log(service);
    };

    const onChange2 = (e) => {
        e.persist();
        handleCategoryName(e.target.value);
        console.log(e.target.value);
        setService({ ...service, categoryName: e.target.value });
        ///////////////////////////////////////////////////////////////////////////
    };

    const onChangefile = (e) => {
        setFile(e.target.files[0]);
        setFilename(e.target.files[0].name);
    };

    const onSubmitFile = async (e, fields) => {
        console.log('onSubmitFile');
        console.log(service)
        e.preventDefault();
        if (service.JobTitle === '' || service.description === '' || service.Salary === '' || filename === '') {
            alert('Need to fill inputs');
            return; // stop the function
        }
        let result = [];
        for (let i = 0; i < service.length; i += 1) {
            result[i] = service[i].value;
            if (service[i].value == '') { // check if value is empty
                alert('Need to fill inputs');
                return; // stop the function
            }

        }
        const formData = new FormData();
        console.log(service);
        formData.append('file', file);
        formData.append('document', JSON.stringify(service));
        formData.append('user', JSON.stringify(user));

        var config1 = {
            headers: {
                'Content-Type': 'multipart/form-data',
                Authorization: user.auth.token,
            },
        };
        axios
            .post('https://www.togedoortestgo.site/graphql/services/uploadfile', formData, config1)

            .then((response) => {
                console.log(response);
                history.push('/');
            })
            .catch((error) => {
                console.log(error);
            });
    };



    return (
        <DashboardCard title='Post new project' className='mb-5'>

            <Form onSubmit={onSubmitFile}>
                <FormGroup>
                    <Label for='exampleSelect1'>Project category</Label>
                    <Input type='select' name='categoryName' id='exampleSelect1' onChange={onChange2}>
                        {categories.map((category, key) => (
                            <option value={category.name.split(' ').join('-')}>{category.name}</option>
                        ))}
                    </Input>
                </FormGroup>
                <FormGroup>
                    <Label for='exampleSelect1'>Sub category</Label>
                    <Input type='select' name='subCategoryID' id='exampleSelect1' onChange={onChange}>
                        {subCategories.length &&
                            subCategories.map((subCategory, key) => (
                                <option value={subCategory._id} key={subCategory._id}>
                                    {subCategory.name}
                                </option>
                            ))}
                    </Input>
                </FormGroup>
                <FormGroup>
                    <Label for='exampleEmail1'>Project title</Label>
                    <Input type='text' name='JobTitle' onChange={onChange} />
                </FormGroup>

                <Row form>
                    <Col md={12}>

                        <FormGroup>
                            <Label for='exampleSelect3'>Price</Label>
                            <Input type='number' name='Salary' onChange={onChange}></Input>

                        </FormGroup>

                    </Col>

                    <Col md={12}>
                        <FormGroup>
                            <Label for='exampleSelect3' className='form-label'>
                                About this project
                            </Label>
                            <textarea
                                type='text'
                                className='form-control'
                                name='description'
                                rows='5'
                                onChange={onChange}></textarea>

                        </FormGroup>


                        <Label for='exampleSelect3' className='form-label'>
                            {' '}
                            PROJECT IMAGE:{' '}
                        </Label>
                    </Col>
                    <Col md={12}>
                        <FormGroup onSubmit={onSubmitFile}>
                            <Input type='file' className='custom-file-input' id='customFile' onChange={onChangefile} />
                            <Label for='customFile' className='custom-file-label' htmlFor='customFile'>
                                {filename}
                            </Label>
                        </FormGroup>
                    </Col>
                    <Col md={12}>
                        <div className='text-center'>
                            <Button type='submit' color='primary' className='outline-hover--effect '>
                                Post new project
                            </Button>
                        </div>
                    </Col>
                </Row>

            </Form>
        </DashboardCard>
    );
};
PostNewJob.propTypes = {
    user: PropTypes.object.isRequired,
    isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, {})(PostNewJob);
